import TopBar from '../../layout/TopBar/TopBar'
import SectionHeader from '../../views/SectionHeader/SectionHeader'

import AOS from 'aos'
import 'aos/dist/aos.css'

import styles from './AboutUs.module.scss'
import { useEffect } from 'react'

const AboutUs = () => {
	useEffect(() => {
		AOS.init()
	}, [])

	return (
		<div className={styles.root}>
			<TopBar src='../logo.jpg' />
			<div className={styles.container}>
				<SectionHeader header={'O nas'} />
				<p className={styles.text} data-aos='fade-left'>
					Witajcie w wiecie naszej amatorskiej ligi piki nonej! Jestemy pasjonatami futbolu, ktrzy postanowili
					stworzy to miejsce dla wszystkich mionikw piki nonej w naszej spoecznoci. Nasza liga zostaa zaoona
					z myl o tym, aby stworzy przyjazne rodowisko dla wszystkich, ktrzy kochaj ten pikny sport i chc si
					razem bawi oraz rywalizowa. Niezalenie od tego, czy jeste zawodnikiem, kibicem czy po prostu osob
					zainteresowan wydarzeniami w naszej lidze, ta strona jest dedykowana dla Ciebie. Tutaj znajdziesz najwiesze
					informacje na temat naszych meczw, zawodnikw, statystyk oraz wiele innych ciekawych treci zwizanych z
					pik non. Docz do naszej pasjonujcej przygody pikarskiej i bd czci naszej ligowej spoecznoci!
				</p>
			</div>
		</div>
	)
}

export default AboutUs
